/* eslint-disable no-underscore-dangle */

import { klass, MediaConstants } from '@accedo/xdk-core';
import HTML5Player from '@accedo/xdk-core/esm/device/player/HTML5Player';

const {
    DRM: { AES_128, PLAYREADY, WIDEVINE },
    PROTOCOL: { DASH, HLS, PROGRESSIVE, SMOOTH_STREAMING }
} = MediaConstants;

const Player = klass.create(
    HTML5Player,
    {},
    {
        getCapabilities() {
            return {
                protocol: [PROGRESSIVE, HLS, SMOOTH_STREAMING, DASH],
                drm: [AES_128, PLAYREADY, WIDEVINE]
            };
        }
    }
);

export default Player;
