/* eslint-disable no-underscore-dangle, no-unused-expressions, max-len */
import { klass, MediaConstants, PlayerConstants } from '@accedo/xdk-core';
import HTML5Player from '@accedo/xdk-core/esm/device/player/HTML5Player';
import logger from '@accedo/xdk-log';

const {
    DRM: { AES_128, PLAYREADY, WIDEVINE },
    PROTOCOL: { DASH, HLS, PROGRESSIVE, SMOOTH_STREAMING }
} = MediaConstants;

const {
    BUFFERING: { FINISH: BUFFERING_FINISH },
    EVENT: { BUFFERING }
} = PlayerConstants;

const isPositiveNumber = (n) => n >= 0;

const { debug } = logger({
    logTag: '[core/device/player/HTML5Player]'
});

const PlayerOverrides = {
    getCapabilities() {
        return {
            protocol: [PROGRESSIVE, HLS, SMOOTH_STREAMING, DASH],
            drm: [AES_128, PLAYREADY, WIDEVINE]
        };
    },
    // The following 3 functions are alternate implementations for the methods of the same name provided by the XDK
    // to fix an issue where a flicker of the start of the asset is displayed when we begin playback with a resume
    // see https://itvplc.jira.com/browse/FED-2282

    /* Original _onSeeked function being swizzled
  _onSeeked() {
    debug(`_onSeeked`);
    this.dispatchEvent(BUFFERING, BUFFERING_FINISH);
  }
  */
    _afterSeek: false,
    _onSeeked() {
        debug('_onSeeked');
        this.dispatchEvent(BUFFERING, BUFFERING_FINISH);
        this._afterSeek = true;
    },
    /* Original _onPlaying function being swizzled
  _onPlaying() {
    debug(`_onPlaying`);
    this.dispatchEvent(BUFFERING, BUFFERING_FINISH);
  }
  */
    _onPlaying() {
        debug('_onPlaying');
        this.dispatchEvent(BUFFERING, BUFFERING_FINISH);

        const { _afterSeek } = this;

        // show video if we previously started with a seek
        if (_afterSeek === true) {
            this._show();
        }

        this._afterSeek = false;
    },
    /* Original function _doPlay being swizzled
  _doPlay() {
    var _this$_playerObject4;

    debug(`_doPlay`);

    this._show();

    (_this$_playerObject4 = this._playerObject) === null || _this$_playerObject4 === void 0 ? void 0 : _this$_playerObject4.play();
  }
  */
    _doPlay() {
        const { _seekTime } = this;

        debug('_doPlay');

        // don't show video if we are starting with a seek
        if (!isPositiveNumber(_seekTime)) {
            this._show();
        }

        this._playerObject?.play();
    },
    /* Original function _doStop being swizzled
  _doStop() {
    var e;
    debug("_doStop"), this.__onLoaded = !1, null === (e = this._playerObject) || void 0 === e || e.pause(), this._hide(), debug("_doStop > _isReady"), this._isReady() && !this._isEnded() && this._setCurrentTime(0), this.__hideSubtitle()
  }
  */
    _doStop() {
        debug('_doStop');

        this.__onLoaded = false;

        if (this._playerObject) {
            this._playerObject.pause();
            this._playerObject.src = null;
        }
        this._hide();
        this.__hideSubtitle();
    }
};

const Player = klass.create(HTML5Player, {}, PlayerOverrides);

export { PlayerOverrides };
export default Player;
